.markdown-content {
  img {
    background-color: var(--bg-white);
    box-sizing: content-box;
    max-width: 100%;

    &[align="right"] {
      padding-left: 20px;
    }

    &[align="left"] {
      padding-right: 20px;
    }
  }

  .emoji {
    background-color: transparent;
    max-width: none;
    vertical-align: text-top;
  }

  span.frame {
    display: block;
    overflow: hidden;

    > span {
      border: 1px solid var(--gray-200);
      display: block;
      float: left;
      margin: 13px 0 0;
      overflow: hidden;
      padding: 7px;
      width: auto;
    }

    span img {
      display: block;
      float: left;
    }

    span span {
      clear: both;
      color: var(--color-gray-dark);
      display: block;
      padding: 5px 0 0;
    }
  }

  span.align-center {
    clear: both;
    display: block;
    overflow: hidden;

    > span {
      display: block;
      margin: 13px auto 0;
      overflow: hidden;
      text-align: center;
    }

    span img {
      margin: 0 auto;
      text-align: center;
    }
  }

  span.align-right {
    clear: both;
    display: block;
    overflow: hidden;

    > span {
      display: block;
      margin: 13px 0 0;
      overflow: hidden;
      text-align: right;
    }

    span img {
      margin: 0;
      text-align: right;
    }
  }

  span.float-left {
    display: block;
    float: left;
    margin-right: 13px;
    overflow: hidden;

    span {
      margin: 13px 0 0;
    }
  }

  span.float-right {
    display: block;
    float: right;
    margin-left: 13px;
    overflow: hidden;

    > span {
      display: block;
      margin: 13px auto 0;
      overflow: hidden;
      text-align: right;
    }
  }
}
